import formatCalendar from './calendar.js';
let calendar={
	getLunar(year,month,date){
		return formatCalendar.solar2lunar(year,month,date);
	},
	formatNum(n){
		return n<10?'0'+n:n+''
	},
	submit(lunar = 'solar', year, month, day, hour, min, isHourShow = true, isMinShow = true){
		let hourArray = [
			// '', 
			'23-1',
			'1-3', 
			'3-5', 
			'5-7',
			'7-9',
			'9-11',
			'11-13',
			'13-15',
			'15-17',
			'17-19',
			'19-21',
			'21-23'
		];
		
		if(lunar === 'solar'){
			let lYear = parseInt(year);
			let lMonth = month;
			let lDay = parseInt(day);
			let toValues = formatCalendar.solar2lunar(lYear, parseInt(lMonth), lDay);
 			
			if(isHourShow === true && isMinShow === true) 
				return {
					text:toValues.lYear + '' + toValues.lMonth + '' + toValues.lDay + '' + hourArray[hour] + min + '',
					year:toValues.lYear,
					month:toValues.lMonth,
					day:toValues.lDay,
					hour: hour - 1,
					min: min
				}
				
			if(isHourShow === true && isMinShow === false) 
				return {
					text:toValues.lYear + '' + toValues.lMonth + '' + toValues.lDay + '' + hourArray[hour],
					year:toValues.lYear,
					month:toValues.lMonth,
					day:toValues.lDay,
					hour: hour - 1,
					min: "00"
				}
			
			return {
				text:toValues.lYear + '' + toValues.lMonth + '' + toValues.lDay + '',
				year:toValues.lYear,
				month:toValues.lMonth,
				day:toValues.lDay,
				hour: "00",
				min: "00"
			}
		}
		
		if(lunar === 'lunar'){
			let isLeap = false;
			if(typeof(month) === 'string'){
				isLeap = month.indexOf('') !== -1 ? true : false;
				if(isLeap) month = parseInt(month.substr(1));
			}
			
			let toValues = formatCalendar.lunar2solar(year, parseInt(month), day, isLeap);
			
			if(isHourShow === true && isMinShow === true) 
				return {
					text:toValues.cYear + '' + toValues.cMonth + '' + toValues.cDay + '' + hourArray[hour] + min + '',
					year:toValues.cYear,
					month:toValues.cMonth,
					day:toValues.cDay,
					hour: hour - 1,
					min: min
					
				}
			if(isHourShow === true && isMinShow === false) 
				return {
					text:toValues.cYear + '' + toValues.cMonth + '' + toValues.cDay + '' + hourArray[hour],
					year:toValues.cYear,
					month:toValues.cMonth,
					day:toValues.cDay,
					hour: hour - 1,
					min: "00"
				}
			
			return {
				text:toValues.cYear + '' + toValues.cMonth + '' + toValues.cDay + '',
				year:toValues.cYear,
				month:toValues.cMonth,
				day:toValues.cDay,
				hour: "00",
				min: "00"
			}
		}
	},
	init(lunar = 'solar', startDate, endDate, year, month, day, hour, min, isHourShow = true, isMinShow = true, isTab = false, lunarType = 'number'){
		if(lunar === 'solar') return calendar.calendarSolar(startDate, endDate, year, month, day, hour, min, isHourShow, isMinShow, isTab, lunarType)
		if(lunar === 'lunar') return calendar.calendarLunar(startDate, endDate, year, month, day, hour, min, isHourShow, isMinShow, isTab, lunarType);
		
	},
	calendarSolar(startDate, endDate, year, month, day, hour = '0', min, isHourShow, isMinShow, isTab, lunarType){
		let cYear = parseInt(year);
		let cMonth = month;
		let cDay = parseInt(day);
		
		if(isTab){
			let isLeap = false;
			if(typeof(cMonth) === 'string'){
				isLeap = cMonth.indexOf('') !== -1 ? true : false;
				if(isLeap) cMonth = parseInt(month.substr(1));
			}
			let toValues = formatCalendar.lunar2solar(cYear, cMonth, cDay, isLeap);
			
			cYear = toValues.cYear;
			cMonth = toValues.cMonth;
			cDay = toValues.cDay;
		}
		cMonth = parseInt(cMonth);
		
		//
		let yearArray = [];
		let yearIndex = 0;
		let solarIndex = 0;
		for (let i = startDate; i <= endDate; i++) {
		  yearArray.push(i);

		  if(i === cYear) yearIndex = solarIndex;
		  
		  solarIndex++
		}
		
		//
		let monthArray = [];
		let monthIndex = 0;
		for (let m = 1; m <= 12; m++) {
			monthArray.push(m);
			
			if(m === cMonth) monthIndex = m - 1;
		}
		
		//
		let dayNum = formatCalendar.solarDays(cYear, cMonth);
		let dayArray = [];
		let dayIndex = 0;
		for (let d = 1; d <= dayNum; d++) {
			dayArray.push(d);
			
			if(d === cDay) dayIndex = d - 1;
		}
		
		//
		let hourArray = [];
		let hourIndex = isTab ? parseInt(hour) - 1 : parseInt(hour) >= 0 ? parseInt(hour) + 1 : 0;
		if(isHourShow){
			hourArray = [
				// '', 
				'23-1',
				'1-3', 
				'3-5', 
				'5-7',
				'7-9',
				'9-11',
				'11-13',
				'13-15',
				'15-17',
				'17-19',
				'19-21',
				'21-23'
				
			];
		}
		
		//
		let minArray = [];
		let minIndex = parseInt(min) > 0 ? parseInt(min) : 0;
		if(isMinShow){
			for (let f = 0; f <= 60; f++) {
				minArray.push(f);
			}
		}
		
		return {
			range: [yearArray, monthArray, dayArray, hourArray, minArray],
			years:yearArray,
			yearIndex:yearIndex,
			months: monthArray,
			monthIndex: monthIndex,
			days: dayArray,
			dayIndex: dayIndex,
			hours:hourArray,
			hourIndex: hourIndex,
			mins:minArray,
			minIndex: minIndex
		};
	},
	calendarLunar(startDate, endDate, year, month, day, hour = '0', min, isHourShow, isMinShow, isTab, lunarType){
		let lYear = parseInt(year);
		let lMonth = month;
		let lDay = parseInt(day);
		let isLeap = false;
		// if(typeof(lMonth) === 'string'){
		// 	isLeap = lMonth.indexOf('') !== -1 ? true : false;
		// 	if(isLeap) lMonth = parseInt(lMonth.substr(1));
		// }

		if(isTab){
			let toValues = formatCalendar.solar2lunar(lYear, parseInt(lMonth), lDay);
			lYear = toValues.lYear;
			lMonth = toValues.lMonth;
			lDay = toValues.lDay;
			isLeap = toValues.isLeap;
		}
		lMonth = parseInt(lMonth);
		
		//
		let yearArray = [];
		let yearIndex = 0;
		let solarIndex = 0;
		for (let i = startDate; i <= endDate; i++) {
		  if(lunarType == 'number'){
			  yearArray.push(i); //
		  }else{
			 yearArray.push(formatCalendar.toGanZhiYear(i));// 
		  }

		  if(i === lYear) yearIndex = solarIndex;
		  
		  solarIndex++
		}
		
		/**
		 * 
		 * 1
		 * 2
		 * 3
		 */
		let monthArray = [];
		let isMonthR = formatCalendar.leapMonth(lYear);
		let monthIndex = isLeap ? lMonth : (lMonth < isMonthR ? lMonth : lMonth - 1);
		let mMax = 12;//12
		if(isMonthR > 0) mMax = 13;//13
		for (let m = 1; m <= mMax; m++) {
			if(isMonthR > 0){
				if(m == isMonthR + 1){
					//
					if(lunarType == 'number'){
						monthArray.push('' + isMonthR);//
					}else{
						monthArray.push('' + formatCalendar.toChinaMonth(isMonthR))//
					}
				}else if(m > isMonthR + 1){
					if(lunarType == 'number'){
						monthArray.push(m - 1);//
					}else{
						monthArray.push(formatCalendar.toChinaMonth(m - 1))//
					}
				}else{
					if(lunarType == 'number'){
						monthArray.push(m);//
					}else{
						monthArray.push(formatCalendar.toChinaMonth(m))//
					}
				}
			}else{
				if(lunarType == 'number'){
					monthArray.push(m);//
				}else{
					monthArray.push(formatCalendar.toChinaMonth(m))//
				}
			}
		}
		
		/**
		 * 
		 * 1
		 * 2
		 */
		let dayNum = isLeap ? formatCalendar.leapDays(lYear) : formatCalendar.monthDays(lYear, lMonth);
		let dayArray = [];
		let dayIndex = 0;
		for (let d = 1; d <= dayNum; d++) {
			if(lunarType == 'number'){
				dayArray.push(d);//
			}else{
				dayArray.push(formatCalendar.toChinaDay(d));//
			}
			
			if(d === lDay) dayIndex = d - 1;
		}
		
		//
		let hourArray = [];
		let hourIndex = isTab ? parseInt(hour) : parseInt(hour) >= 0 ? parseInt(hour) + 1 : 0;
		if(isHourShow){
			hourArray = [
				// '', 
				'23-1',
				'1-3', 
				'3-5', 
				'5-7',
				'7-9',
				'9-11',
				'11-13',
				'13-15',
				'15-17',
				'17-19',
				'19-21',
				'21-23'
			];
		}
		
		//
		let minArray = [];
		let minIndex =  parseInt(min) > 0 ? parseInt(min) : 0;
		if(isMinShow){
			for (let f = 0; f <= 60; f++) {
				minArray.push(f);
			}
		}
		
		return {
			range: [yearArray, monthArray, dayArray, hourArray, minArray],
			years:yearArray,
			yearIndex:yearIndex,
			months: monthArray,
			monthIndex: monthIndex,
			days: dayArray,
			dayIndex: dayIndex,
			hours:hourArray,
			hourIndex: hourIndex,
			mins:minArray,
			minIndex: minIndex
		};
	}
}
export default calendar